.profile__name {
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    color: #fff;
    margin: 0;
    padding: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 336px;
}

@media screen and (max-width: 767px) {
    .profile__name {
        font-size: 27px;
        line-height: 33px;
        max-width: 189px;
    }
}